import { defineComponent } from "vue";
import AcPageContent from "@/components/page-content";
import AcPageModal from "@/components/page-modal";
import AcPageSearch from "@/components/page-search";
import { tableConfig } from "./config/table-config";
import { modalConfig } from "./config/modal-config";
import { formConfig } from "./config/search-config";
import { usePageModal } from "@/hooks/usePageModal";
import { usePageSearch } from "@/hooks/usePageSearch";
import { computed } from "@vue/reactivity";
import { useStore } from "@/store";
export default defineComponent({
  name: "GoodsInfo",
  components: {
    AcPageContent,
    AcPageModal,
    AcPageSearch
  },

  setup() {
    const [acModalRef, defaultInfo, handleCreateClick, handleEditClick] = usePageModal();
    const [acPageContentRef, handleResetClick, handleQueryClick] = usePageSearch();
    const store = useStore();
    const modalConfigRef = computed(() => {
      const categoryIdItem = modalConfig.formItems.find(item => item.field === "categoryId");
      categoryIdItem.options = store.state.entireCategory.map(item => ({
        title: item.name,
        value: item.id
      }));
      return modalConfig;
    });
    const formConfigRef = computed(() => {
      const categoryIdItem = formConfig.formItems.find(item => item.field === "categoryId");
      categoryIdItem.options = store.state.entireCategory.map(item => ({
        title: item.name,
        value: item.id
      }));
      return formConfig;
    });
    return {
      acModalRef,
      defaultInfo,
      handleCreateClick,
      handleEditClick,
      acPageContentRef,
      handleResetClick,
      handleQueryClick,
      tableConfig,
      modalConfigRef,
      formConfigRef
    };
  }

});